___CSS_LOADER_EXPORT___.push([module.id, ".container[data-v-16e8463a]{max-width:1200px}.shadow-2xl[data-v-16e8463a]{box-shadow:0 25px 50px -12px rgba(0,0,0,.25)}@keyframes gradient-shift-16e8463a{0%{background-position:0 50%}50%{background-position:100% 50%}to{background-position:0 50%}}", "",{"version":3,"sources":["webpack://./resources/js/pages/Login.vue"],"names":[],"mappings":"AAuLA,4BACE,gBACF,CAGA,6BACE,4CACF,CAGA,mCACE,GAAK,yBAA4B,CACjC,IAAM,4BAA8B,CACpC,GAAO,yBAA4B,CACrC","sourcesContent":["<template>\n  <div class=\"min-h-screen bg-gray-100\">\n    <Head :title=\"__('Log In')\" />\n\n    <!-- Hero Section with Curved Background -->\n    <div class=\"relative min-h-[50vh] pt-5 pb-44 mx-3 rounded-xl overflow-hidden\"\n         style=\"background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\">\n      <!-- Overlay -->\n      <div class=\"absolute inset-0 bg-black bg-opacity-60\"></div>\n\n      <!-- Curved Background Effect -->\n      <div class=\"absolute inset-0 overflow-hidden\">\n        <div class=\"absolute -bottom-10 -left-10 w-96 h-96 rounded-full bg-white bg-opacity-10 transform rotate-12\"></div>\n        <div class=\"absolute -top-10 -right-10 w-80 h-80 rounded-full bg-white bg-opacity-5 transform -rotate-12\"></div>\n        <div class=\"absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-[800px] h-[400px] rounded-full bg-gradient-to-r from-purple-400 to-pink-400 opacity-20 blur-3xl\"></div>\n      </div>\n\n      <!-- Content -->\n      <div class=\"relative z-10 container mx-auto px-4\">\n        <div class=\"flex justify-center\">\n          <div class=\"lg:w-5/12 text-center mx-auto\">\n            <h1 class=\"text-white text-4xl font-bold mb-4 mt-20\">{{ __('Welcome Back!') }}</h1>\n            <p class=\"text-white text-lg leading-relaxed\">\n              Use these awesome forms to login or create new account in your project for free.\n            </p>\n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!-- Login Card Container -->\n    <div class=\"container mx-auto px-4\">\n      <div class=\"flex justify-center -mt-32 relative z-20\">\n        <div class=\"xl:w-4/12 lg:w-5/12 md:w-7/12 w-full max-w-md\">\n          <!-- Login Card -->\n          <div class=\"bg-white rounded-xl shadow-2xl border-0 relative overflow-hidden\">\n            <!-- Card Header -->\n            <div class=\"text-center pt-6 pb-2\">\n              <h5 class=\"text-xl font-semibold text-gray-800\">{{ __('Log In') }}</h5>\n            </div>\n\n            <!-- Card Body -->\n            <div class=\"px-6 pb-6\">\n              <form @submit.prevent=\"attempt\" class=\"space-y-4\">\n                <!-- Email Input -->\n                <div>\n                  <input \n                    v-model=\"form.email\"\n                    type=\"email\" \n                    class=\"w-full px-4 py-3 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all\"\n                    :class=\"{ 'border-red-500': form.errors.has('email') }\"\n                    :placeholder=\"__('Email Address')\"\n                    id=\"email\"\n                    name=\"email\"\n                    autofocus=\"\"\n                    required\n                  >\n                  <HelpText class=\"mt-2 text-red-500\" v-if=\"form.errors.has('email')\">\n                    {{ form.errors.first('email') }}\n                  </HelpText>\n                </div>\n\n                <!-- Password Input -->\n                <div>\n                  <input \n                    v-model=\"form.password\"\n                    type=\"password\" \n                    class=\"w-full px-4 py-3 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all\"\n                    :class=\"{ 'border-red-500': form.errors.has('password') }\"\n                    :placeholder=\"__('Password')\"\n                    id=\"password\"\n                    name=\"password\"\n                    required\n                  >\n                  <HelpText class=\"mt-2 text-red-500\" v-if=\"form.errors.has('password')\">\n                    {{ form.errors.first('password') }}\n                  </HelpText>\n                </div>\n\n                <!-- Remember Me Toggle and Forgot Password -->\n                <div class=\"flex items-center justify-between\">\n                  <div class=\"flex items-center\">\n                    <Checkbox\n                      @change=\"() => (form.remember = !form.remember)\"\n                      :model-value=\"form.remember\"\n                      dusk=\"remember-button\"\n                      :label=\"__('Remember me')\"\n                    />\n                  </div>\n\n                  <div v-if=\"supportsPasswordReset || forgotPasswordPath !== false\">\n                    <Link\n                      v-if=\"forgotPasswordPath === false\"\n                      :href=\"$url('/password/reset')\"\n                      class=\"text-gray-500 font-bold no-underline text-sm\"\n                      v-text=\"__('Forgot your password?')\"\n                    />\n                    <a\n                      v-else\n                      :href=\"forgotPasswordPath\"\n                      class=\"text-gray-500 font-bold no-underline text-sm\"\n                      v-text=\"__('Forgot your password?')\"\n                    />\n                  </div>\n                </div>\n\n                <!-- Sign In Button -->\n                <div class=\"pt-2\">\n                  <Button\n                    class=\"w-full py-3 px-4 bg-gradient-to-r from-blue-500 to-cyan-500 text-white font-medium rounded-lg hover:from-blue-600 hover:to-cyan-600 transform hover:scale-105 transition-all duration-200 shadow-lg\"\n                    type=\"submit\"\n                    :loading=\"form.processing\"\n                  >\n                    <span>\n                      {{ __('Log In') }}\n                    </span>\n                  </Button>\n                </div>\n              </form>\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport Auth from '@/layouts/Auth'\nimport { Button, Checkbox } from 'laravel-nova-ui'\n\nexport default {\n  name: 'LoginPage',\n\n  layout: Auth,\n\n  components: {\n    Checkbox,\n    Button,\n  },\n\n  data: () => ({\n    form: Nova.form({\n      email: '',\n      password: '',\n      remember: false,\n    }),\n  }),\n\n  methods: {\n    async attempt() {\n      try {\n        const { redirect } = await this.form.post(Nova.url('/login'))\n\n        let path = { url: Nova.url('/'), remote: true }\n\n        if (redirect !== undefined && redirect !== null) {\n          path = { url: redirect, remote: true }\n        }\n\n        Nova.visit(path)\n      } catch (error) {\n        if (error.response?.status === 500) {\n          Nova.error(this.__('There was a problem submitting the form.'))\n        }\n      }\n    },\n  },\n\n  computed: {\n    supportsPasswordReset() {\n      return Nova.config('withPasswordReset')\n    },\n\n    forgotPasswordPath() {\n      return Nova.config('forgotPasswordPath')\n    },\n  },\n}\n</script>\n\n<style scoped>\n/* Additional custom styles if needed */\n.container {\n  max-width: 1200px;\n}\n\n/* Soft UI Shadow Effects */\n.shadow-2xl {\n  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);\n}\n\n/* Gradient Background Animation */\n@keyframes gradient-shift {\n  0% { background-position: 0% 50% }\n  50% { background-position: 100% 50% }\n  100% { background-position: 0% 50% }\n}\n</style>\n"],"sourceRoot":""}]);
